

// background
.bg-light {
  background: $gray-1 !important;
}

.bg-white {
	background: $white !important;
}

// color
.text-success {
  color: $green !important;
}
.text-warning{
	color: $orange !important;
}

.text-danger{
	color: $red !important;
}

.freelance{
	color: $green;
}

.temporary{
	color: $red;
}

.full-time{
	color: $blue;
}

.part-time{
	color: $orange;
}


// border-radius
 
 .border-radius{
 	border-radius: $border-radius !important;
 }

 .border-radius-top{
 	border-top-left-radius: $border-radius !important;
    border-top-right-radius: $border-radius !important;
}

.border-radius-right{
	border-top-right-radius: $border-radius !important;
    border-bottom-right-radius: $border-radius !important;
}
.border-radius-bottom {
    border-bottom-right-radius: $border-radius !important;
    border-bottom-left-radius: $border-radius !important;
}

.border-radius-left {
    border-top-left-radius:  $border-radius !important;
    border-bottom-left-radius:  $border-radius !important;
}


// add-listing

.add-listing .login a{
	color: $white;
}

.add-listing .login a:hover{
	color: $primary;
}
.add-listing .login a i{
	color: $primary;
}

.add-listing .btn-white i{
	color: $primary;
}

.add-listing .btn-white:hover{
	border-color: $primary;
}
.add-listing .btn-white:focus i,
.add-listing .btn-white:hover i{
	color: $white;
}
// section-title

.section-title{
	margin-bottom: 30px;
}
.section-title .title{
	padding-bottom: 15px;
	margin-bottom: 20px; 
	position: relative;
}
.section-title .title:before{
	position: absolute; 
	content: ""; 
	height: 3px; 
	width: 100px; 
	background:$primary; 
	bottom: 0;
	left: 0;
}
.section-title.center {
	text-align: center;
}
.section-title.center .title:before{
	left: 50%;
	margin-left:-50px ;

}

.job-list{
	padding: 30px 20px;
	background: $white;
	transition: all 0.3s ease-in-out;
	display: flex;
	border-bottom: 1px solid $border-color;
	align-items: center;
}

.job-list .job-list-logo{
	margin-right: 25px; 
	flex: 0 0 80px;	
	border: 1px solid $border-color;
	height: 80px;
	width: 80px;
	line-height: 80px;
	text-align: center;
}
.job-list-title{
	margin-bottom: 5px;
}
	
.job-list-details ul {
	display: flex; 
	flex-wrap: wrap;
	margin-bottom: 0px;
}
.job-list-details ul li{
	margin: 5px 10px 5px 0px;
	font-size: 13px;
}
.job-list-details ul li.freelance a{
	color: $green; 
}
.job-list-details ul li.temporary a{
	color: $red; 
}
.job-list-details ul li.full-time a{
	color: $blue; 
}
.job-list-details ul li.part-time a{
	color: $orange; 
}


.job-list .job-list-favourite-time{
	margin-left: auto;
	text-align: center;	
	font-size: 13px;
}
.job-list .job-list-favourite-time{
	flex: 0 0 100px;
	
}
.job-list-favourite-time .job-list-favourite {
	display: inline-block;
	position: relative; 
	height: 40px; 
	width: 40px; 
	line-height: 40px;
	border: 1px solid $border-color;
	border-radius: 100%;
	text-align: center;
	transition: all 0.3s ease-in-out;
	margin-bottom: 20px;
	font-size: 16px;
	color: $gray-3;
}
.job-list-favourite-time span{
	display: block; 
	margin: 0 auto;
}
.job-list-favourite-time .job-list-favourite:hover{
	background: $white;
	color:$red;
}
.job-list:hover{
	-webkit-box-shadow: 0px 0px 24px 4px rgba($gray-9, 0.04); 
	-moz-box-shadow: 0px 0px 24px 4px rgba($gray-9, 0.04);
	box-shadow: 0px 0px 24px 4px rgba($gray-9, 0.04);
	position: relative;
    z-index: 9;
}

.job-list:hover a.job-list-favourite {
    color: $red;
    -webkit-box-shadow: -1px 4px 10px 1px rgba($blue, 0.1);
    -moz-box-shadow: -1px 4px 10px 1px rgba($blue, 0.1);
    box-shadow: -1px 4px 10px 1px rgba($blue, 0.1);
}

//job-list hovering
.job-list.hovering{
	-webkit-box-shadow: 0px 0px 24px 4px rgba($gray-9, 0.04); 
	-moz-box-shadow: 0px 0px 24px 4px rgba($gray-9, 0.04);
	box-shadow: 0px 0px 24px 4px rgba($gray-9, 0.04); 
	position: relative;
    z-index: 9;
}

.job-list.hovering .job-list-favourite-time .job-list-favourite{
	background: $white;
	color:$red;
	border: 1px solid $red;
}








// jobber-post-box-grid

.job-list.job-grid{
	padding-top: 40px; 
	padding-bottom: 0px;
	border: 1px solid $border-color;
	border-radius: $border-radius;
