.accordion {
	.accordion-item {
		.accordion-title {
			margin-bottom: 15px;
			font-size: 16px;
			font-weight: 500;

			.collapsed {
				color: $gray-8;
			}

		}

		.accordion-content {
			padding-bottom: 25px;
		}

	}

}


// Accordion Style
.accordion-style {
	.card {
		border: 2px solid $border-color;
		margin-bottom: 10px;
		border-radius: $border-radius;
		&:last-child {
			margin-bottom: 0px;
		}
	}
	.card-header {
		background: transparent;
		border-bottom: none;
		.accordion-title {
			display: flex;
			align-items: center;
			i {
				font-size: 12px;
				margin-left: auto;
			}
		}
	}
	.accordion-content {
		border-top: 1px solid $border-color;
	}
	.btn {
		display: block;
		color: $primary;
		font-size: 16px;
		padding: 0px;
		text-decoration: none;
		width: 100%;
		text-align: left;
		+ {
			i {
				color: $primary;
			}
		}
	}
	.btn.collapsed {
		color: $gray-8;
		+ {
			i {
				color: $gray-8;
			}
		}
	}
}

.accordion-style .btn.collapsed:hover,
.accordion-style .btn{
 color: $primary;
} 

.accordion-style .btn.btn-link:hover,
.accordion-style .btn.btn-link:focus{
 color: $primary;
}

.accordion-style .accordion-content li{ 
	margin-bottom: 10px; 
}
.accordion-style .accordion-content li:last-child{ 
	margin-bottom: 0px; 
}