.bg-overlay-gradient {
  position: relative;
  &:before {
    z-index: 0;
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    content: "";
    background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.6) 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(0, 0, 0, 0)), color-stop(100%, rgba(0, 0, 0, 0.6)));
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.6) 100%);
    background: -o-linear-gradient(top, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.6) 100%);
    background: -ms-linear-gradient(top, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.6) 100%);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.6) 100%);
    top: auto;
    height: 75%;
    transition: all .35s;
  }
}

// avatar
.avatar {
  width: 50px;
  height: 50px;
  display: inline-block;
}
.avatar.avatar-sm {
  width: 20px;
  height: 20px;
}
.avatar.avatar-md {
  width: 40px;
  height: 40px;
}
.avatar.avatar-lg {
  width: 60px;
  height: 60px;
}
.avatar.avatar-xl {
  width: 80px;
  height: 80px;
}
.avatar.avatar-xll {
  width: 100px;
  height: 100px;
}
.avatar.avatar-xlll {
  width: 120px;
  height: 120px;
}
.avatar.avatar-xllll {
  width: 180px;
  height: 180px;
}

// font size
.font-sm {
  font-size: 13px;
}
.font-md {
  font-size: 15px;
}
.font-lg {
  font-size: 17px;
}
.font-xl {
  font-size: 21px;
}
.font-xll {
  font-size: 30px;
  line-height: 30px;
}
.font-xlll {
