.irs-bar {
	background: $primary;
	height: 6px;
}

.irs-slider {
	background: $primary;
	background: $white;
	height: 12px;
	width: 12px;
	border: 2px solid $primary;
	border-radius: 100%;
	cursor: pointer;
}

.irs-line {
	height: 6px;

	.irs-line-mid {
		background: $gray-2;
	}

	.irs-line-left {
		background: $gray-2;
	}

	.irs-line-right {
		background: $gray-2;
	}

}

.irs-from {
	color: $primary;
	background: transparent;
	font-size: 11px;

	&:after {
		content: none;
	}

}

.irs-to {
	color: $primary;
	background: transparent;
	font-size: 11px;

	&:after {
		content: none;
	}

}

.irs-single {
	color: $primary;
	background: transparent;
	font-size: 11px;

	&:after {
		content: none;
	}

}

.irs-min {
	display: none;
}

.irs-max {
	display: none;
}
