.header {
  background: $white;
  .container-fluid {
    padding: 0 60px;
    align-items: initial;
  }
}


// topbar
.header .topbar {
  background: $gray-9;
  padding: 10px 0;
}

.header .topbar {
  a {
    color: $white;

    &:hover {
      color: $primary;
    }

  }

}

.header .topbar  .dropdown {
  margin-right: 20px;

  .dropdown-toggle {
    padding: 12px 0;
    i {
      font-size: 10px;
    }
  }

  .dropdown-menu {
    a {
      color: $gray-8;

      &:hover {
        color: $primary;
      }

    }

  }

}

.header .topbar .social {
  margin-right: 20px;
  display: inline-block;

  ul {
    margin: 0;

    li {
      display: inline-block;
      padding: 0 4px;

      a {
        color: $white;

        &:hover {
          color: $primary;
        }

      }

    }

  }

}

.header .topbar .login {
  display: inline-block;

  a {
    color: $white;

    &:hover {
      color: $primary;
    }

  }

}

// topbar dropdown-menu
.topbar .dropdown .dropdown-menu {
  border-radius: 0;
  border: none;
  @include box-shadow(0px, 5px, 10px, rgba(0, 0, 0, 0.1));
  padding: 10px;
}

.topbar .dropdown .dropdown-menu a {
  font-size: 14px;
  padding: 10px 20px;
  font-weight: 500;
  border-radius: 3px;
}

.topbar .dropdown .dropdown-menu a:hover {
  background: rgba($primary, 0.1);
  color: $primary;
}


// navbar

.navbar {
  padding: 0;
}

.navbar .navbar-nav .nav-link {
  font-weight: 500;
  font-size: 16px;
  padding: 10px 20px;
  color: $white;
  text-transform: capitalize;

  i {
    font-weight: bold;
  }

  &:hover {
    color: $primary;
  }

}

.navbar-collapse {
  position: relative;
}

.navbar-light {
  color: #fff;
  background-color: #ccc6c6;
  border-color: #aca1a2;
}

.navbar-light .navbar-nav > li > a {
  color: #fff;
}

.navbar-light .navbar-nav > .dropdown > a .caret {
  border-top-color: #fff;
  border-bottom-color: #fff;
}

.navbar-brand {
  padding: 20px 0px;
  margin-right: 0;
}

.navbar-brand img {

  height: 40px;
}

.navbar-light .navbar-brand {
  color: #fff;
  flex: 0 0 180px;
}

.header .navbar-nav > li {
  display: flex;
}

.header .navbar-nav li > a {
  align-items: center;
  display: flex;
  padding: 3px 0px;
  color: $gray-8;
  text-transform: capitalize;
  font-size: 14px;
}

.header .navbar-nav li > a:hover {
  color: $primary;
}

.header .navbar-nav li > a i {
  margin-left: 5px;
  font-size: 10px;
}

.header .add-listing {
  align-self: center;
  flex: 0 0 300px;
  text-align: right;
}


.megamenu {
  padding: 20px 20px;
  width: 100%;
}



.megamenu > div > li > ul {
  padding: 0;
  margin: 0;
}

.megamenu > div > li > ul > li {
  list-style: none;
}

.megamenu .card {
  outline: none;
}

.megamenu .card:hover, .megamenu .card:focus {
  outline: 1px solid #000;
}

.megamenu > div > li > ul > li > a {
  display: block;
  padding: 3px 20px;
  clear: both;
  font-weight: normal;
  line-height: 1.428571429;
  color: #333333;
  white-space: normal;
}

.megamenu > div > li > ul > li > a:hover, .megamenu > div > li > ul > li > a:focus {
  text-decoration: none;
  color: #262626;
  background-color: #f5f5f5;
}

.megamenu.disabled > a, .megamenu.disabled > a:hover, .megamenu.disabled > a:focus {
  color: #999999;
}

.megamenu.disabled > a:hover, .megamenu.disabled > a:focus {
  text-decoration: none;
  background-color: transparent;
  background-image: none;
  cursor: not-allowed;
}

.megamenu.dropdown-header {
  color: #428bca;
  font-size: 18px;
}



.header li > .dropdown-item:focus, .header li > .dropdown-item:hover {
  color: $primary;
  background: none;
}

.header .dropdown-item.active, .header .dropdown-item:active, .header .dropdown-item:focus, .header .dropdown-item:hover {
  background: none;
}

.header .dropdown-toggle::after {
  content: none;
}

.header .navbar-collapse {
  align-items: inherit;
  margin-left: 40px;
}

.header .megamenu .dropdown-item {
  padding: 0px;
}


.header .navbar .dropdown-menu a.dropdown-item {
  min-width: 180px;

}

.header .navbar .dropdown-menu {
  padding: 15px;
}

.header .navbar .dropdown-menu li a i {
  margin-left: auto;
  }

.navbar .dropdown > .dropdown-menu li > a {
  font-size: 14px;
  padding: 10px 20px 10px 0;
  border-radius: 3px;
  position: relative;
}


.navbar .dropdown > .dropdown-menu li > a:hover {
  background: rgba($primary, 0.1);
  color: $primary;
  padding-left: 15px;
}

// header-transparent

.header-transparent {
  position: absolute;
  background: transparent;
  width: 100%;
  z-index: 999;
}

.header-transparent .navbar .navbar-nav .nav-link {
  color: $white;

  &:hover {
    color: $primary;
  }

}

.navbar .navbar-nav .nav-item.active .nav-link {
  color: $primary;
}
.navbar .navbar-nav .dropdown-menu li.active > a {
    background: rgba($primary, 0.1);
    color: $primary;
    padding-left: 15px;
}

.header-transparent .add-listing {
  align-self: inherit;
  flex: inherit;
  text-align: right;
}

.header-transparent .login a {
  color: $white;

  &:hover {
    color: $primary;
  }

}

// add-listing

 .header .add-listing .login a{
  color: $white;
}

 .header .add-listing .login a:hover{
  color: $primary;
}
.header .add-listing .login a i{
  color: $primary;
}

.header .add-listing .btn-white i{
  color: $primary;
}

.header .add-listing .btn-white:hover,
.header .add-listing .btn-white:focus{
  border-color: $primary;
}
.header .add-listing .btn-white:focus i,
.header .add-listing .btn-white:hover i{
  color: $white;
}




@media (min-width: 992px) {

  .header .navbar .dropdown-menu {
  margin: 0px;
  font-size: 14px;
  border-radius: 0px;
  border: none;
  left: 100%;
  transform: translate3d(-50%, 10px, 0);
  transition: all 0.3s ease-in-out;
  transition-property: opacity, visibility, transform;
  transform-origin: top center;
  @include box-shadow(0px, 5px, 10px, rgba(0, 0, 0, 0.1));
  visibility: hidden;
  opacity: 0;
   display: block !important;

}



.header .navbar .dropdown-menu.megamenu {
  left: 50%;
}


.navbar-nav .dropdown-menu.dropdown-menu-lg {
    min-width: 540px;
}

.navbar-nav .mega-menu {
    position: static;
}


.header .navbar .dropdown:hover > .dropdown-menu {
  transform: translate3d(-50%, 0, 0);
  visibility: visible;
  opacity: 1;
}


  .header .navbar .dropdown-menu .dropdown-submenu .dropdown-menu {
  left: 100%;
 right: auto;
 transform: translate3d(15px, 10px, 0);
  }

.navbar-nav li:hover > ul.dropdown-menu {
        visibility: visible;
        opacity: 1;

}



.dropdown-submenu {
    position:relative;
}
.dropdown-submenu>.dropdown-menu {
    top:0;
    left:100%;
    margin-top:-6px;
}

.dropdown-menu > li > a:hover:after {
    text-decoration: underline;
    transform: rotate(-90deg);
}


}

@media (max-width: 1400px) {

  .navbar .navbar-nav .nav-link{
        padding: 10px 12px;
  }
}

@media (max-width: 991px) {

.header .container-fluid {
    padding: 0 20px;
}

.navbar-toggler {
    border: none;
    position: absolute;
    height: 75px;
    right: 10px;
}

.header .navbar-collapse{
  position: absolute;
    top: 100%;
    z-index: 9999;
    background: $white;
    width: 100%;
    left: 0;
    margin-left: 0px;
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
    @include box-shadow(0px, 5px, 10px, rgba(0, 0, 0, 0.1));
}
.navbar .navbar-nav {
    padding: 10px 0;
}
.navbar .navbar-nav .nav-link{
  color: $gray-8;
}
.navbar .navbar-nav .nav-link i {
    margin-left: auto;
}
.header .navbar-nav > li {
    display: block;
}


.header .navbar .dropdown-menu {
    margin: 0px;
    font-size: 14px;
    border-radius: 0px;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    background:#fafafa;
    padding: 14px 30px;
    font-size: 13px;

}

.header .add-listing {
    margin-right: 40px;
}


}

@media (max-width: 575px) {
  .header .add-listing {
    margin-right: 0;
    padding-bottom: 20px;
    display: flex;
    align-items: center;
    flex: 0 0 100%;
}
.header .add-listing a.btn{
  margin-left: auto;
}



}




.place-card-large { display: none !important; }
