
.bg-holder-pattern{
	background-repeat: no-repeat;
	background-position: 100% 15%;
}

.feature-info {
    padding: 25px;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
    border-radius: $border-radius;
}


.feature-info .feature-info-icon {
    font-size: 56px;
    line-height: 56px;
    display: inline-block;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
    color: $primary;
    
}

.feature-info .feature-info-content p{
    margin-bottom: 5px;
}

.feature-info a{
    text-decoration: underline;
    font-weight: 600;
}

.feature-info a i {
    padding-left: 10px;
}

.feature-info.bg-primary a {
    color: $gray-9;
}

.feature-info.bg-dark a {
    color: $primary;
}

.feature-info.bg-primary a:hover,
.feature-info.bg-dark a:hover {
    color: $white;
}



.feature-info:hover{
    background: $primary;
}

.feature-info:hover .feature-info-icon {
    color: $white;
}

.feature-info:hover .feature-info-content,
.feature-info:hover .feature-info-content h5{
    color: $white;
}

.feature-info-border{
    border: 1px solid $gray-1;
}

.feature-info-border .feature-info-icon {
    border: 1px solid $gray-1;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    line-height: 80px;
    font-size: 30px;
}

.feature-info-border:hover .feature-info-icon {
    border: 1px solid $white;
    background: $white;
    color: $primary;

}


.feature-info-02,
.feature-info-04, {
    display: flex;
}

.feature-step{
    padding: 0;
}

.feature-step .feature-info-icon {
    width: 150px;
    height: 150px;
    line-height: 150px;
    border-radius: 50%;
    font-size: 40px;
    display: inline-block;
    margin-bottom: 15px;
    background: $gray-1;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
    color: $black;
}

.feature-step-01 .feature-info-icon{
	background: none;
}

.feature-step .feature-info-icon:before{
	content: inherit;
}

.feature-step .feature-info-content{
    padding: 10px 20px;
}

.feature-step:hover{
    background: none;
}

.feature-step:hover .feature-info-icon{
    color: $primary;
}

.feature-step:hover .feature-info-content h5{
    color: $black;
}
.feature-step:hover .feature-info-content p{
    color: $body-color;
}


.step-number {
  margin-bottom: 30px;
  font-weight: bold;
  font-size: 18px;
  color: $primary;
  position: relative;
}

.step-number span:after {
    content: "";
    border-top: 3px dotted rgba($border-color, 0.2);
    width: 92%;
    position: absolute;
    height: 20px;
    top: 12px;
    left: 58%;
}

.row > [class*='col-']:last-child .step-number span:after {
  content: none;
}



.feature-step-01 .feature-info-icon{background: inherit; border-radius: 0; position: relative;  line-height: inherit;}
.feature-step-01 .feature-info-icon img{border-radius: 50%;}
.feature-step-01 .feature-info-icon:before{
    content: "";
    background: $gray-9;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    position: absolute;
    left: 0px;
    top: 0px;
    border-radius: 50%;
}
.feature-step-01 .feature-info-icon:after{
    content: "\f002";
    font-family: "Font Awesome 5 Free";
    font-size: 40px;
    font-weight: 600;
    position: absolute;
    left: 50%;
    top: 50%;
    color: $white;
    opacity: 0;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}
.feature-step:hover .feature-info-icon:after{
   opacity: 1; 
}


//******** image block ********//



.popup-icon{
    position: relative;
    display: block;
}

.popup-icon i{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    // width: 40px;
    // height: 40px;
    // line-height: 40px;
    // text-align: center;
    // border: 2px solid $primary;
    // border-radius: 50%;
    font-size: 50px;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    transition: all 0.5s ease;

}
.popup-icon i:hover {
color: #ffff;
}


/******* Banner *******/


.banner {
    padding: 220px 0;
    position: relative;
}

.job-search-item {
    text-align: left;
}

.job-search-item .form-group{
    position: relative;
}

.job-search-item span{
    font-size: 11px;
}

.job-search-item label {
    font-weight: 500;
}

.job-search-item input{
    padding-left: 40px;
    height: 60px;
    box-shadow: none;
    border: none;
}

.job-search-item i{
    left: 15px;
    top: 50%;
    right: auto;
    font-size: 16px;
    position: absolute;
    color:  $primary;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    transform: translateY(-50%);
}

.job-search-item .btn{
    padding: 18px 20px !important;
    margin-top: 29px;
    padding-left: 40px !important;
    position: relative;
}

.job-search-item .btn i{
    color: $white;
}

.job-tag ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
}

.job-tag  li {
    margin-left: 20px;
    margin-bottom:5px;
}

.job-tag li:first-child {
    margin-left: 0;
}

.job-tag  li a{
	color:$white;
}

.job-tag  li:hover a{
	color:$primary;
}


@media (max-width: 991px) {

.job-search-item .btn{
    margin-top: 0px;
}


}

@media (max-width: 767px) {

.bg-holder-pattern{
	background-image: inherit!important;
}

.step-number {
    margin-bottom: 10px;
}  

.step-number span:after{
    content: none;
}

.lead{
    font-size: 16px;
}


}


@media (max-width: 575px) {

.feature-info-02,
.feature-info-04, {
    display: block;
}

.job-img-01{
    padding-right: 0px;

    }

.job-img-02 {
    margin-top: 20px;
}

.popup-icon i{
	left: 50%;
}



}

