.category-style{
	display: flex;
	flex-wrap: wrap;
}

.category-item {
	flex: 0 0 25%;
	max-width: 25%;
	transition: all 0.3s ease;
	position: relative;
	cursor: pointer;
	border-right: 1px solid $gray-1;
	padding: 30px 20px;
	color: $gray-3;
}


.category-item:nth-child(1n+5) {
    border-top: 1px solid $gray-1;
}

.category-item:nth-child(4n+0) {
    border-right: 0;
}

.category-icon {
    width: 50px;
    height: 50px;
    display: inline-block;
}

.category-icon i{
	font-size: 50px;
	line-height: 50px;
	color: $primary;
}

.category-item:hover {
    box-shadow: 0px 10px 25px -6px rgba(0,0,0,0.15)!important;
}


@media (max-width: 767px) {

.category-item {
    flex: 0 0 50%;
    max-width: 50%;
}

.category-item:nth-child(1n+3) {
    border-top: 1px solid $gray-1;
}

.category-item:nth-child(2n+0) {
    border-right: 0;
}


}

@media (max-width: 575px) {

.category-item {
    flex: 0 0 100%;
    max-width: 100%;
}

.category-item:nth-child(1n+2) {
    border-top: 1px solid $gray-1;
}

.category-item:nth-child(1n+0) {
    border-right: 0;
}


}
