// browse-jobs
.browse-job {
	border-bottom: 1px solid $border-color;
	align-items: center;
}

// category home 02
.category-style-02 ul li {
	margin-bottom: 15px;
}

.category-style-02 ul li:last-child {
	margin-bottom: 0px;
}

.category-style-02 ul li a {
	display: flex;
	align-items: center;
}

.category-style-02 ul li .category-title {
	font-size: 14px;
	margin-bottom: 0;
	transition: all 0.3s ease-in-out;
}

.category-style-02 ul li .category-count {
	background: $gray-1;
	padding: 4px 12px;
	border-radius: $border-radius;
	color: $gray-3;
	margin-left: auto;
	transition: all 0.3s ease-in-out;
}

.category-style-02 ul li:hover a .category-count {
	background: $primary;
	color: $white;
}

.category-style-02 ul li:hover .category-title {
	color: $primary;
}

// job-found
.job-found {
	display: flex;
	align-items: center;
}

.job-found span {
	background: $primary;
	padding: 5px 10px;
	border-radius: $border-radius;
	color: $white;
	font-size: 14px;
}

