// nav-tabs
.nav-tabs {
	border: none;

	.nav-item {
		margin-bottom: 0;

		.nav-link {
			background: transparent;
			border: none;
			color: $gray-8;
			padding: 10px 30px;
			border-radius: $border-radius;
			margin-right: 20px;
		}

		.nav-link.active {
			background: $primary;
			border: none;
			color: $white;
		}

	}

}

// nav-tabs-02

.select-border .select2-container--default .select2-selection--single .select2-selection__arrow:before {
	color: $gray-3;
}

.nav-tabs.nav-tabs-02 .nav-item .nav-link {
	background: transparent;
	color: $gray-3;
	border-radius: 0px;
	padding: 12px 14px 18px;
	border-bottom: 3px solid transparent;
	margin: 0px 30px;
}

.nav-tabs .nav-item .nav-link {
	color: $gray-3;
	font-weight: 600;
}

.nav-tabs.nav-tabs-02 .nav-item .nav-link.active {
	background: transparent;
	color: $primary;
	border-radius: 0px;
	border-bottom: 3px solid $primary;
}

// nav-tabs-03
.nav-tabs.nav-tabs-03 li {
	position: relative;
}

.nav-tabs.nav-tabs-03 li:last-child:after {
	border: none;
}

.nav-tabs.nav-tabs-03 li:after {
	position: absolute;
	content: "";
	border: 1px dashed #c1c1c1;
	left: 50%;
	top: 32%;
	width: 100%;
}

.nav-tabs.nav-tabs-03 li i {
	border: none;
}

.nav-tabs.nav-tabs-03 .feature-info-icon {
	background: $white;
	border-radius: 50%;
	text-align: center;
	height: 70px;
	width: 70px;
	line-height: 70px;
	margin: 0 auto;
	font-size: 30px;
	border: 1px solid $gray-2;
	position: relative;
	z-index: 99;
}

.nav-tabs.nav-tabs-03 .feature-info-icon.active {
	background: $primary;
	border-radius: 50%;
	text-align: center;
	height: 70px;
	width: 70px;
	line-height: 70px;
	color: $white;
	font-size: 30px;
	margin: 0 auto;
	border: 1px solid $primary;
}

.nav-tabs.nav-tabs-03 .nav-item span {
	font-weight: 600;
	color: $gray-3;
}

.nav-tabs.nav-tabs-03 .nav-item.active .feature-info-icon {
	background: $primary;
	color: $white;
	border-color: $primary;
}

.nav-tabs.nav-tabs-03 .nav-item.active span {
	color: $primary;
}

// @media992
@media (max-width:1024px) {
	.nav-tabs.nav-tabs-02 .nav-item .nav-link {
		margin: 0px 10px;
		padding: 12px 14px 18px;
	}

}

// @media767
@media (max-width:767px) {
	.nav-tabs.nav-tabs-02 .nav-item .nav-link {
		margin: 0px 0px;
		padding:12px 13px 10px;
	}

	ul#myTab {
		margin-bottom: 30px;
		margin-top: 20px;
	}

	.tab-pane .border-right {
		border: none !important;
	}

	.browse-job {
		display: block !important;
		text-align: center;
	}

	.job-found {
		margin-bottom: 20px;
		justify-content: center;
	}

}

// @media575
@media (max-width:575px) {
	.nav-tabs.nav-tabs-02 .nav-item .nav-link {
		margin: 0px 0px;
	}

	.nav-tabs.nav-tabs-02 .nav-item .nav-link {
		padding: 12px 14px 18px !important;
	}

	.nav-tabs .nav-item .nav-link {
		padding: 10px 20px !important;
		margin-bottom: 10px;
		margin-right: 5px;
	}

	.nav-tabs.nav-tabs-03 li {
		margin-bottom: 20px;
	}

	.nav-tabs.nav-tabs-03 li:after {
		border: none;
	}

}

// @media575
@media (max-width:479px) {
	.nav-tabs .nav-item .nav-link {
		padding: 6px 10px !important;
		margin-bottom: 10px;
		margin-right: 2px;
	}

}