.pricing-plan {
    // flex: 0 0 33.33%;
    // max-width: 33.33%;
    border: 1px solid $gray-1;
    position: relative;
    margin: 20px 0;
    cursor: pointer;
    padding-bottom: 25px;
}

.pricing-plan.active {
    z-index: 2;
    top: 0;
    padding: 20px 0px 50px;
    -moz-border-radius: 5px;
    margin: 0 -1px;
    -webkit-box-shadow: 0px 0px 12px 2px rgba(0, 0, 0, 0.04) !important;
    -moz-box-shadow: 0px 0px 12px 2px rgba(0, 0, 0, 0.04) !important;
    box-shadow: 0px 0px 12px 2px rgba(0, 0, 0, 0.04) !important;
}

.pricing-price {
    border-bottom: 1px solid $gray-1;
    padding: 25px;
    padding-bottom: 20px;
}

.pricing-price sup {
    top: -20px;
    font-size: 16px;
    left: -5px;
}

.pricing-price strong {
    color: $gray-9;
    font-size: 50px;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    transition: all 0.5s ease;
    line-height: 50px;
}

.pricing-list li {
	border-bottom: 1px solid $gray-1;
    padding: 15px;
}

.pricing-list li:nth-child(even) {
    background:  $gray-1;
}


.pricing-plan:hover .btn,
.pricing-plan.active .btn {
    background: $primary;
    color: $white;
    border-color: $primary;
}

.pricing-plan:hover .pricing-price strong, .pricing-plan.active .pricing-price strong{
	color: $primary;
}



@media (max-width: 767px) {

.pricing-plan{display: block;}
.pricing-plan{max-width: 100%;}

}