body {
	font-family: $font-base;
	font-weight: normal;
	font-style: normal;
	font-size: 14px;
	color: $body-color;
	overflow-x: hidden;
}

a {
	outline: medium none !important;
	color: $primary;
	transition: all 0.3s ease-in-out;

	&:focus {
		color: $primary;
		text-decoration: none !important;
	}

	&:hover {
		color: $primary;
		text-decoration: none !important;
	}

}

input {
	outline: medium none !important;
	color: $primary;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $font-hedding;
	font-weight: normal;
	color: $gray-8;
	margin-top: 0px;
	font-weight: 600;

	a {
		color: inherit;
	}

}

label {
	font-weight: normal;
}

h1 {
	font-size: 42px;
	font-style: normal;
}

h2 {
	font-size: 28px;
	line-height: 38px;
}

h3 {
	font-size: 26px;
}

h4 {
	font-size: 22px;
}

h5 {
	font-size: 18px;
	// font-weight: 500;
}

h6 {
	font-size: 16px;
}

p {
	font-weight: normal;
	line-height: 1.7;
}

*::-moz-selection {
	background: $primary;
	color: $white;
	text-shadow: none;
}

::-moz-selection {
	background: $primary;
	color: $white;
	text-shadow: none;
}

::selection {
	background: $primary;
	color: $white;
	text-shadow: none;
}

.container-fluid {
	padding:  0 100px;
}

// form-control
.form-control {
	border: 1px solid $border-color;
	color: $gray-4;
	border-radius: 0px;
	height: 50px;
	padding: 10px 20px;
	box-shadow: none;
	font-size: 14px;
	font-weight: 500;
	border-radius: $border-radius;

	&:focus {
		box-shadow: none;
		border-color: $primary;
	}
}

// input-group-prepend
.input-group-prepend, .input-group-append {
	.input-group-text {
		width: 50px;
		background: transparent;
		border-color: $border-color;
		box-shadow: none;
		justify-content: center;
	}

}

// checkbox
.custom-control-input {
	&:checked~.custom-control-label {
		&:before {
			background: $primary;
			border-color: $primary;
		}
	}
	&:not(:disabled) {
		&:active~.custom-control-label {
			&:before {
				background: transparent;
				border-color: transparent;
			}
		}
	}
	&:focus~.custom-control-label {
		&:before {
			box-shadow: none;
		}
	}
	&:focus {
		&:not(:checked)~.custom-control-label {
			&:before {
				border-color: $border-color;
			}
		}
	}
}
.custom-control-label {
	&:before {
		top: 2px;
	}
	&:after {
		top: 2px;
	}
}
.custom-checkbox {
	.custom-control-label {
		&:before {
			border-radius: $border-radius;
			border: 2px solid $gray-2;
		}
	}
}

// border
.border {
	border-color: $border-color !important;
}
.border-top {
	border-top-color: $border-color !important;
}
.border-left {
	border-left-color: $border-color !important;
}
.border-right {
	border-right-color: $border-color !important;
}
.border-bottom {
	border-bottom-color: $border-color !important;
}

// badge
.badge {
	border-radius: $border-radius;
	+ {
		.badge {
			margin-left: 6px;
		}
	}
}
.badge-md {
	padding: 5px 8px;
	font-size: 13px;
	font-weight: normal;
}
.badge-primary {
	background: $primary;
}

// back-to-to
.back-to-top {
	background: $primary;
	color: $white;
	width: 50px;
	height: 50px;
	line-height: 50px;
	font-size: 16px;
	display: inline-block;
	border-radius: 50%;

	&:hover {
		color: $white;
	}

	&:focus {
		color: $white;
	}

}

// breadcrumb
.breadcrumb {
	padding-left: 0;
	background: transparent;
	font-family: $font-hedding;
	font-weight: 500;
	display: inline-flex;


	.breadcrumb-item {
		padding-right: 5px;

		a {
			color: $gray-3;

			&:hover {
				color: $primary;
			}

		}

		i {
			padding-right: 5px;
			font-size: 11px;
		}

		&:before {
			display: none;
		}

	}

	.active {
		span {
			color: $primary;
		}

	}

}




 // blockquote
.blockquote {
	font-size: 16px;
	border-left: 4px solid $border-color;
	padding-left: 20px;
	font-style: italic;
	display: block;
}

// table
.table-bordered {
	td {
		border-color: $border-color;
	}
	th {
		border-color: $border-color;
	}
}
.table {
	thead {
		th {
			border-bottom-color: $border-color;
		}
	}
}
.table-striped {
	tbody {
		tr {
			&:nth-of-type(odd) {
				background: $gray-1;
			}
		}
	}
}

