// blog
.blog-post .blog-post-details {
	padding: 25px;
}

// blog-post 02
.blog-post .blog-post-details .blog-post-time{
	margin-bottom: 15px;
}

.blog-post .blog-post-details .blog-post-time a {
	color: $gray-3;
	font-size: 13px;
	font-weight: 500;
}

.blog-post .blog-post-details .blog-post-category {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.blog-post .blog-post-category a,
.blog-detail .blog-post .blog-post-category a {
	color: $primary;
	font-weight: 600;
}

.blog-post .blog-post-footer {
	display: flex;
	justify-content: center;
	padding: 20px 0px;
	border-top: 2px solid $border-color;
}

.blog-post .blog-post-footer a {
	font-size: 12px;
	color: $body-color;
}

.blog-post .blog-post-footer i {
	padding-right: 5px;
	color: $primary;
}

.blog-post .blog-post-footer a:hover,
.blog-post .blog-post-footer a:hover i {
	color: $primary;
}

.blog-post .blog-post-footer .blog-post-author img {
	height: 20px;
	border-radius: 50%;
	margin: 0 5px;
	width: auto;
	display: inline-block;
}

.blog-post .blog-post-footer .blog-post-time,
.blog-post-author,
.blog-post-time,
.blog-post-share {
	margin: 0 6px;
}

.blog-post .blog-post-details .blog-post-description {
	margin: 15px 0px;
}

// blog-post-quote
.blog-post-quote .blog-post-details .blog-post-category a {
	color: $white;
}

.blog-post-quote .blog-post-details .blog-post-link .btn-link {
	color: $white;
}

.blog-post-quote .blog-post-details .blog-post-link .btn-link:hover {
	color: $gray-9;
}

.blog-post-quote .blog-post-content .blog-post-footer a,
.blog-post-quote .blog-post-content .blog-post-footer i {
	color: $white;
	transition: all 0.3s ease-in-out;
}

.blog-post-quote .blog-post-content .blog-post-footer span,
.blog-post-quote .blog-post-details .blog-post-title h5 a {
	color: $white;
}

.blog-post-quote .blog-post-content .blog-post-footer a:hover,
.blog-post-quote .blog-post-content .blog-post-footer a:hover i,
.blog-post-quote .blog-post-details .blog-post-title h5 a:hover {
	color: $gray-9;
}

.blog-post-quote .blockquote {
	padding: 40px 40px 0px 40px;
	margin-bottom: 0;
	border-left: 0;
}

.blog-post-quote .blockquote i {
	font-size: 30px;
	margin-bottom: 20px;
	color: rgba($gray-9, 0.5);
}

.blog-post-quote .blog-post-content .blog-post-footer {
	border-top: 2px solid rgba($gray-9, 0.1);
}

// blog-post-you-tube //

.blog-post.blog-post-you-tube iframe {
	width: 100%;
	height: 385px;
}

// post-without-image

.post-without-image {
	border: 2px solid $border-color;
}

// sidebar

.blog-sidebar .widget {
	margin-bottom: 50px;
}

.blog-sidebar .widget .widget-title {
	margin-bottom: 20px;
}

.blog-sidebar .widget .search {
	position: relative;
}

.blog-sidebar .widget .search i {
	position: absolute;
	right: 0;
	top: 0;
	background: $primary;
	color: $white;
	padding: 18px 20px;
	cursor: pointer;
	border-radius: 0 3px 3px 0px;
}

.blog-sidebar .widget .search input {
	padding-right: 65px;
	border-radius: $border-radius;
}

.blog-sidebar .widget ul.list-style li a {
	display: flex;
	color: $gray-4;
	width: 100%;
}

.blog-sidebar .widget ul.list-style li a:hover {
	color: $primary;
}

.blog-sidebar .widget ul.list-style li {
	margin-bottom: 20px;
}

// recent-posts
.blog-sidebar .widget .recent-posts a b {
	color: $gray-9;
	font-weight: 500;
}

.blog-sidebar .widget .recent-posts a:hover b {
	color: $primary;
}

// newsletter

.newsletter {
	position: relative;
}

.blog-sidebar .widget .newsletter i {
	color: $primary;
	background: inherit;
}

.blog-sidebar .widget .newsletter .form-check,
.blog-sidebar .widget .newsletter label,
.form-row .custom-control.custom-checkbox label {
	cursor: pointer;
}

.blog-sidebar .widget .newsletter .newsletter-bg-icon i {
	font-size: 160px;
	color: $white;
	opacity: 0.1;
	position: absolute;
	right: -20px;
	bottom: -35px;
}

// Subscribe & Follow

.blog-sidebar .widget .social ul li {
	padding: 10px;
	border: 1px solid $border-color;
	display: flex;
	margin-bottom: 10px;
	align-items: center;
}

.blog-sidebar .widget .social ul li a i {
	width: 15px;
}

.blog-sidebar .widget .social ul li a {
	font-size: 12px;
	font-weight: 600;
}

.blog-sidebar .widget .social ul .facebook a {
	color: #466ca9;
}

.blog-sidebar .widget .social ul .facebook .follow:hover {
	background: #466ca9;
	color: $white;
}

.blog-sidebar .widget .social ul .twitter a {
	color: #20b5e6;
}

.blog-sidebar .widget .social ul .twitter .follow:hover {
	background: #20b5e6;
	color: $white;
}

.blog-sidebar .widget .social ul .youtube a {
	color: #d92c20;
}

.blog-sidebar .widget .social ul .youtube .follow:hover {
	background: #d92c20;
	color: $white;
}

.blog-sidebar .widget .social ul .instagram a {
	color: #181818;
}

.blog-sidebar .widget .social ul .instagram .follow:hover {
	background: #181818;
	color: $white;
}

.blog-sidebar .widget .social ul .linkedin a {
	color: #13799f;
}

.blog-sidebar .widget .social ul .linkedin .follow:hover {
	background: #13799f;
	color: $white;
}

.blog-sidebar .widget .social ul li .follow {
	background: $gray-1;
	padding: 3px 18px;
	font-size: 12px;
	font-weight: 300;
}

// popular-tag

.blog-sidebar .widget .popular-tag ul li {
	display: inline-block;
	margin-bottom: 4px;
}

.blog-sidebar .widget .popular-tag ul li a {
	padding: 6px 20px;
	display: block;
	border: 1px solid $border-color;
	color: $body-color;
}

.blog-sidebar .widget .popular-tag ul li a:hover {
	color: $primary;
	border-color: $primary;
}

// blog-detail

.navigation .nav-links {
	display: flex;
}

.navigation .nav-links .nav-previous a {
	display: flex;
	color: $body-color;
}

.navigation .nav-links .nav-previous {
	width: 50%;
	border: 1px solid $border-color;
	margin-right: 30px;
	transition: all 0.5s ease-in-out;
}

.navigation .nav-links .nav-previous .pagi-text {
	border-right: 1px solid $border-color;
	transition: all 0.5s ease-in-out;
}

.navigation .nav-links .pagi-text {
	display: inline-block;
	padding: 12px 25px;
	color: $body-color;
	transition: all 0.5s ease-in-out;
}

.navigation .nav-links .nav-title {
	padding: 12px 20px;
}

.navigation .nav-links .nav-next {
	width: 50%;
	border: 1px solid $border-color;
	margin-right: 30px;
	margin-right: 0;
	text-align: right;
	transition: all 0.5s ease-in-out;
}

.navigation .nav-links .nav-next a {
	display: flex;
	color: $body-color;
	justify-content: flex-end;
}

.navigation .nav-links .nav-next .pagi-text {
	display: flex;
	border-left: 1px solid $border-color;
}

.navigation .nav-links .nav-previous:hover .pagi-text,
.navigation .nav-links .nav-next:hover .pagi-text {
	background: $primary;
	color: $white;
}

.navigation .nav-links .nav-previous:hover,
.navigation .nav-links .nav-next:hover {
	background: $gray-1;
}

.navigation .nav-links .nav-next .nav-title,
.navigation .nav-links .nav-previous .nav-title {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.blog-detail .blog-post .blog-post-category {
	display: inline-flex;
}

// blockquote

.blog-detail .blog-post .blog-post-content .blockquote {
	font-size: 16px;
	border-left: 10px solid $primary;
	padding-left: 30px;
	font-style: italic;
	display: block;
}

// Popular-Tags

.blog-detail .blog-post-tags li {
	padding: 6px 10px;
	border: 1px solid $border-color;
	transition: all 0.5s ease-in-out;
	border-radius: $border-radius;
}

.blog-detail .blog-post-tags li a {
	color: $body-color;
}

.blog-detail .blog-post-tags li:hover {
	background: $primary;
	border: 1px solid $primary;
}

.blog-detail .blog-post-tags li:hover a {
	color: $white;
}

// candidate-detail

.candidate-banner {
	padding: 60px 0;
}

.candidate-banner .candidate-list {
	padding: 0;
	border-bottom: inherit;
}

.candidate-banner .candidate-list:hover {
	box-shadow: none;
	border-bottom: inherit;
}

//secondary-menu//
.secondary-menu {
	padding: 10px;
	border: 2px solid $border-color;
	background: $white;
	border-radius: $border-radius;
	margin-bottom: 30px;
	margin-top: -25px;
}

.secondary-menu ul {
	padding: 0px;
	display: flex;
	margin: 0;
}

.secondary-menu ul li {
	list-style: none;
	padding-right: 10px;
}

.secondary-menu ul li a {
	display: block;
	color: $body-color;
	font-weight: 600;
	text-transform: capitalize;
	padding: 1px 15px;
	border-radius: $border-radius;
}

.secondary-menu ul li a:hover,
.secondary-menu ul li a.active {
	color: $white;
	background: $primary;
}

//jobber timeline//

.jobber-candidate-detail .jobber-candidate-timeline {
	position: relative
}

.jobber-candidate-detail .jobber-candidate-timeline:before {
	content: '';
	position: absolute;
	left: 20px;
	width: 2px;
	top: 5px;
	bottom: 5px;
	height: calc(100% - 5px);
	background-color: $border-color;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item {
	display: table;
	position: relative;
	margin-bottom: 20px;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item .jobber-timeline-cricle:before {
	content: '';
	position: absolute;
	left: 12px;
	width: 20px;
	top: -1px;
	bottom: 5px;
	height: 2px;
	background-color: $border-color;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item .jobber-timeline-cricle {
	border-radius: 50%;
	border: 12px solid white;
	z-index: 1;
	top: 5px;
	left: 9px;
	position: absolute;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item .jobber-timeline-cricle > i {
	font-size: 15px;
	top: -8px;
	left: -7px;
	position: absolute;
	color: $primary;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item .jobber-timeline-info {
	display: table-cell;
	vertical-align: top;
	padding: 5px 0 0 70px;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item .jobber-timeline-info h6 {
	color: $primary;
	margin: 5px 0 0px;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item .jobber-timeline-info span {
	color: $gray-9;
	font-size: 13px;
	font-weight: 500;
}

.jobber-candidate-detail .jobber-candidate-timeline span.jobber-timeline-time {
	color: $body-color !important;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-icon i {
	font-size: 16px;
	color: $gray-9;
}

.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-icon {
	border: 2px solid $border-color;
	width: 42px;
	height: 42px;
	border-radius: 50%;
	line-height: 42px;
	text-align: center;
	background: $white;
	position: relative;
	margin-bottom: 20px;
}


// progress //

.progress {
	position: relative;
	overflow: inherit;
	height: 3px;
	margin: 40px 0px 15px;
	width: 100%;
	display: inline-block;
}

.progress  .progress-bar {
	height: 3px;
	background: $primary;
}

.progress .progress-bar-title {
	position: absolute;
	left: 0;
	top: -30px;
	color: $gray-8;
	font-size: 14px;
	font-weight: 600;
}

.progress  .progress-bar-number {
	position: absolute;
	right: 0;
	color: $body-color;
	top: -30px;
}

// share-box//

.share-box {
	position: relative;
}

.share-box .share-box-social {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	left: 50%;
	bottom: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background: $primary;
	padding: 10px;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	border-radius: 3px;
	-webkit-transform: translate(-50%, 0px);
	transform: translate(-50%, 0px);
}

.share-box .share-box-social li a {
	padding: 0 10px;
	color: $white !important;
	font-size: 13px;
	display: block;

}

.share-box .share-box-social li a:hover {
	color: $gray-9 !important;
}

.share-box .share-box-social li a i {
	color: $white !important;
	transition: all 0.3s ease-in-out;
}

.share-box .share-box-social li a:hover i {
	color: $gray-9 !important;
	transition: all 0.3s ease-in-out;

}

.share-box .share-box-social:before {
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 6px 6px 0 6px;
	border-color: $primary transparent transparent transparent;
	content: "";
	position: absolute;
	left: 50%;
	bottom: -6px;
	z-index: 99;
	margin-left: -3px;
}

.share-box:hover .share-box-social {
	opacity: 1;
	visibility: visible;
	margin-bottom: 6px;
}

.blog-post-quote .blog-post-content .blog-post-footer a:hover span {
	color: $gray-9 !important;
	transition: all 0.3s ease-in-out;
}

// share-box-white-bg //

.share-box.share-white-bg .share-box-social:before {
	border-color: $white transparent transparent transparent;
}

.share-box.share-white-bg .share-box-social {
	background: $white;
	box-shadow: 0px 0px 34px 4px rgba(0, 25, 53, 0.04);
}

.share-box.share-white-bg .share-box-social li a i {
	color: $primary !important;
}

.share-box.share-white-bg .share-box-social li a:hover i {
	color: $gray-9 !important;
	transition: all 0.3s ease-in-out;
}

// iframe //


iframe {
	width: 100%;
	border: 0;
}

/*****************
================================================
(  Media Queries  )
================================================
*******************/
@media (max-width:1400px) {
}

@media (max-width:1199px) {
}

@media (max-width:991px) {
	.blog-post .blog-post-details {
		padding: 20px;
	}

	.blog-post .blog-post-footer {
		padding: 15px;
	}

	.secondary-menu ul li a {
		padding: 1px 10px;
	}

}

@media (max-width:767px) {
	.blog-post-quote .blockquote {
		padding: 20px 20px 0px 20px;
	}

	.progress {
		position: relative;
		overflow: inherit;
		height: 3px;
		margin: 40px 0px 20px;
	}

	.secondary-menu {
		padding: 20px 10px 10px;
	}

	.secondary-menu ul li a {
		padding: 1px 8px;
		margin-bottom: 10px;
	}

	.secondary-menu ul {
		flex-wrap: wrap;
	}

}

@media (max-width:575px) {
	.blog-post .blog-post-details .blog-post-description {
		margin: 15px 0px;
	}

	.blog-detail .blog-post .blog-post-content .blockquote {
		font-size: 14px;
		padding-left: 20px;
	}

	.navigation .nav-links {
		display: block;
	}

	.navigation .nav-links .nav-previous {
		width: 100%;
	}

	.navigation .nav-links .nav-next {
		width: 100%;
		margin-top: 5px;
	}

	.blog-detail .blog-post-tags {
		display: block !important;
	}

	.candidate-banner .candidate-list .candidate-list-details {
		border-bottom: none;
	}

	.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item .jobber-timeline-info {
		padding: 5px 0 0 50px;
	}

	.jobber-candidate-detail .jobber-candidate-timeline .jobber-timeline-item .jobber-timeline-cricle:before {
		display: none;
	}

}

@media (max-width:400px) {
	.blog-post .blog-post-footer {
		display: block;
	}

}
