.login-register { }
.login-register fieldset { border: 1px solid $border-color;border-radius: $border-radius;padding: 10px 20px;}
.login-register legend {font-size: 13px;font-weight: 500;width: auto;padding: 0;}

// tab
.nav-tabs.nav-tabs-border .nav-item .nav-link { background: transparent; padding: 20px 16px;border: 2px solid $border-color;border-radius: $border-radius; border-color: $black;}
.nav-tabs.nav-tabs-border .tab-icon{font-size: 35px; line-height: 35px; font-weight: normal;}
.nav-tabs.nav-tabs-border .nav-item .nav-link p{ font-size: 13px; }
.nav-tabs.nav-tabs-border .nav-item { margin-bottom:10px; flex: 2;}
.nav-tabs.nav-tabs-border .nav-item .nav-link.active { border-color:$primary; color: $primary;}
.nav-tabs.nav-tabs-border .nav-item .nav-link.active h6 { color:$primary;}
.nav-tabs.nav-tabs-border .nav-item .nav-link{margin-right: 20px;}
.nav-tabs.nav-tabs-border .nav-item .nav-link:last-child{margin-right: 0px;}

// form
.login-register .form-group .form-control{border: 1px solid $border-color;}
.login-register .form-group > label{ font-weight: 500; }

// Social Login
.social-login ul { flex-wrap: wrap;}
.social-login ul li{flex: 2;min-width: 49%;margin-right: 2%;margin-bottom: 2%;}
.social-login ul li a{padding: 15px 20px;display: block;color: $white;border-radius: $border-radius;}
.social-login ul li.facebook a{ background: #466ca9; }
.social-login ul li.twitter { margin-right: 0; }
.social-login ul li.twitter a{ background: #20b5e6; }
.social-login ul li.google a{ background: #d34836; }
.social-login ul li.linkedin { margin-right: 0; }
.social-login ul li.linkedin a{ background: #13799f; }

// Job Listing
.inner-banner{ padding: 100px 0; }

.job-shortby .form-inline label{ font-weight: 500;color:$gray-8;  }
.job-filter .select2-container--default .select2-selection--single {border:2px solid $border-color;}

// pagination
.pagination { display: inline-flex; border-radius: $border-radius;border:2px solid $border-color;    padding: 5px 22px;}
.pagination .page-item { margin: 0 6px;display: inline-flex; }
.pagination .page-item .page-link {color: $gray-3;     padding: 8px 16px; border-radius: $border-radius;font-weight: 500; border: none;}
.page-item.active .page-link,.pagination .page-item .page-link:hover { background: $primary;border-color: $primary;color: $white; }

@media (max-width: 991px) {

}

@media (max-width: 767px) {
.social-login ul li { min-width: 100%; }
.login-register .form-control{ height: 40px; }
.pagination .page-item { margin: 0px;}

}

@media (max-width: 575px) {
.job-shortby { margin-top: 20px; }
.nav-tabs.nav-tabs-border .nav-item { margin-right: 0 !important; width: 100%;    flex: inherit;}
.nav-tabs.nav-tabs-border .nav-item .nav-link{padding: 20px 15px;}
.nav-tabs.nav-tabs-border .tab-icon {font-size: 24px;line-height: 24px;}
.job-shortby .filter-btn { margin-top: 30px; }
.pagination { padding: 5px 0; }

}

@media (max-width: 479px) {
.pagination .page-item .page-link { padding: 8px 11px; }
.filter-btn .btn{padding: 12px 25px;}
}

@media (max-width: 320px) {
.social-login ul li a{ padding:10px;}

}









