// testimonial
.testimonial-item .testimonial-content {
	font-size: 28px;
	font-style: italic;
	font-weight: 700;
	position: relative;
}

.testimonial-item .testimonial-name .quotes {
	font-size: 40px;
	line-height: 50px;
	margin-bottom: 10px;
	color: darken($gray-1, 10%);
}

.testimonial-item .avatar {
	width: 100px !important;
	height: 100px !important;
	display: inline-block;
}

.testimonial-item .testimonial-content p {
	font-style: normal !important;
	font-size: 16px;
	font-weight: 500;
	color: $gray-4;
	margin-top: 15px;
}

.owl-nav-top-center .owl-nav .owl-next {
	right: 40%;
	font-size: 32px;
	top: 25px;
	transform: inherit;
	position: absolute;
}

.owl-nav-top-center .owl-nav .owl-prev {
	left: 40%;
	font-size: 32px;
	top: 25px;
	transform: inherit;
	position: absolute;
}

.owl-nav-top-center .owl-nav .owl-prev:hover {
    color: $primary;
    transition: all 0.4s ease;
}
.owl-nav-top-center .owl-nav .owl-next:hover {
    color: $primary;
    transition: all 0.4s ease;
}

// testimonial 02

.testimonial-item-02 {
	overflow: hidden;
}

.testimonial-center .owl-stage-outer {
	overflow: visible;
}

.testimonial-center .owl-item {
	opacity: 0.3;
}

.testimonial-center .owl-item.active {
	opacity: 1;
}

.testimonial-center .owl-item.active .testimonial-content {
	background: $primary;
}

.testimonial-center .owl-item.active .testimonial-content p {
	color: $white;
}

.testimonial-center .testimonial-item-02 .testimonial-content {
	font-size: 20px;
	padding: 40px 40px 30px;
	position: relative;
	transition: all 0.5s ease;
}

.testimonial-center .owl-item.active .testimonial-item-02 .testimonial-content:before {
	opacity: 1;
}

.testimonial-item-02 .testimonial-content p {
	font-style: normal !important;
	font-size: 16px;
	font-weight: 400;
	color: $black;
	margin-top: 15px;
	margin-left: 40px;
}

.testimonial-item-02 .testimonial-content:before {
	position: absolute;
	content: "";
	bottom: -10px;
	width: 0;
	height: 0;
	left: 50%;
	transform: translateX(-50%);
	z-index: 1;
	border-style: solid;
	border-width: 10px 10px 0 10px;
	border-color: #ff8a00 transparent transparent transparent;
	opacity: 0;
}

.testimonial-item-02 {
	margin: 0px 20px;
}

.testimonial-item-02 .testimonial-author {
	margin-top: 30px;
	display: flex;
	align-items: center;
	display: block;
	text-align: center;
}

.testimonial-item-02 .quotes {
	font-size: 30px;
	line-height: 40px;
	position: absolute;
	top: 50px;
	left: 36px;
	color: darken($primary, 15%);
}

.owl-nav-bottom-center .owl-nav .owl-next {
	left: 65%;
	font-size: 32px;
	bottom: 55px;
	transform: inherit;
	position: absolute;
}

.owl-nav-bottom-center .owl-nav .owl-prev {
	right: 65%;
	font-size: 32px;
	bottom: 55px;
	transform: inherit;
	position: absolute;
}

// testimonial-02-small

.testimonial-item-02-small .testimonial-content {
	font-size: 14px;
	padding: 60px 40px 20px;
	margin-top: 10px;
	margin-bottom: 30px;
	border: 1px solid #eeeeee;
}

.testimonial-item-02-small .testimonial-content p {
	font-size: 14px;
}

.testimonial-item-02-small .quotes {
	font-size: 20px;
	top: 26px;
}

.testimonial-item-02-small i {
	font-size: 36px !important;
	line-height: 30px;
	position: absolute;
	top: 50px !important;
	left: 45%;
	color: $gray-2;
}

.testimonial-item-02-small .testimonial-content:before {
	position: absolute;
	content: "";
	bottom: 112px;
	width: 0;
	height: 0;
	left: 50%;
	transform: translateX(-50%);
	z-index: 1;
	border-style: solid;
	border-width: 10px 10px 0 10px;
	border-color: #ffffff transparent transparent transparent;
}

.testimonial-item-02-small .testimonial-content:after {
	position: absolute;
	content: "";
	bottom: 110px;
	left: 50%;
	transform: translateX(-50%);
	width: 0;
	height: 0;
	z-index: 0;
	border-style: solid;
	border-width: 11px 11px 0 10px;
	border-color: #eeeeee transparent transparent transparent;
}

.testimonial-item-02-small .testimonial-content p {
	font-size: 14px;
	font-weight: 400;
	color: #626262;
	margin-top: 15px;
	font-style: italic !important;
}

.testimonial-item-02-small .testimonial-name h6 {
	font-size: 14px;
}

.testimonial-item-02-small .testimonial-name span {
	font-size: 12px;
}

.owl-nav-top-right .owl-nav .owl-next {
	right: 0px;
	top: -43px;
	transform: inherit;
	position: absolute;
	padding: 15px;
}

.owl-nav-top-right .owl-nav .owl-prev {
	right: 40px;
	top: -43px;
	transform: inherit;
	position: absolute;
	padding: 15px;
}

// @media:992

@media (max-width:992px) {
	.owl-nav-top-center .owl-nav .owl-prev {
		left: 35%;
	}

	.owl-nav-top-center .owl-nav .owl-next {
		right: 35%;
	}

	.owl-nav-bottom-center .owl-nav .owl-prev {
		right: 60%;
	}

	.owl-nav-bottom-center .owl-nav .owl-next {
		left: 60%;
	}

}

// @media:767

@media (max-width:767px) {
	.testimonial-content p {
		font-size: 16px;
	}

}

// @media:575

@media (max-width:575px) {
	.owl-nav-top-center .owl-nav .owl-prev {
		left: 30%;
		top: 30px;
	}

	.owl-nav-top-center .owl-nav .owl-next {
		right: 30%;
		top: 30px;
	}

	.testimonial-content p {
		font-size: 14px;
	}

	.testimonial-item {
	}

}

// @media:479

@media (max-width:479px) {
	.testimonial-item {
	}

	.testimonial-item-02 .quotes {
		top: 35px;
		left: 30px;
	}

	.testimonial-center .testimonial-item-02 .testimonial-content {
		padding: 20px 30px 20px;
	}

	.owl-nav-top-center .owl-nav .owl-prev,
	.owl-nav-top-center .owl-nav .owl-next {
		display: none;
	}

	.owl-nav-bottom-center .owl-nav .owl-prev,
	.owl-nav-bottom-center .owl-nav .owl-next {
		display: none;
	}

}