
@media (max-width:991px) {

	h1 {
    font-size: 34px;
    }

	.space-ptb {
		padding: 60px 0;
	}

	.space-pt {
		padding-top: 60px;
	}

	.space-pb {
		padding-bottom: 60px;
	}

	.banner {
    	padding: 100px 0;
    }


}


@media only screen and (min-width:768px) and (max-width:991px) {
	.container {
		max-width: 96%;
	}

}

@media (max-width:767px) {
	h1 {
		font-size: 32px;
	}

	h2 {
		font-size: 24px;
		line-height: 34px;
	}

	h3 {
		font-size: 22px;
	}

	h4 {
		font-size: 20px;
	}

	h5 {
		font-size: 18px;
	}

	h6 {
		font-size: 17px;
	}

	.space-ptb {
		padding: 50px 0;
	}

	.space-pt {
		padding-top: 50px;
	}

	.space-pb {
		padding-bottom: 50px;
	}

	.container {
		max-width: 100%;
	}

}



@media (max-width:575px) {
	h1 {
		font-size: 26px;
	}

	// h2 {
	// 	font-size: 26px;
	// }

	// h3 {
	// 	font-size: 24px;
	// }

	.space-ptb {
		padding: 40px 0;
	}

	.space-pt {
		padding-top: 40px;
	}

	.space-pb {
		padding-bottom: 40px;
	}

	.section-title {
		margin-bottom: 20px;
	}

	.lead {
	    font-size: 14px;
	}

	.banner {
    	padding: 60px 0;
	}

}
